import React, { Component } from 'react';
import firebase from './firebase';

class Entries extends Component {

    state = {
        entries: []
    }

    componentDidMount() {
        const dbRef = firebase.database().ref();
        dbRef.on('value', (snapshot) => {
            const data = snapshot.val();
            const newEntries = [];

            for (let propName in data) {
                const entry = data[propName]
                entry.id = propName

                newEntries.push(entry);
            }
            this.setState({
                entries: newEntries
            })
        })
    }

    deleteEntry = (entryId) => {
        if(window.confirm('Are sure you want to delete this entry? ‚ùå ')){
            const dbRef = firebase.database().ref();
            dbRef.child(entryId).remove();
            alert("Deleted ‚úÖ")
        }
    }

    render() {
        return (
            <div className="wrapper entriesSection">
                {
                    this.state.entries.map((entry) => {
                        return (
                            <div key={entry.id}>
                                <button onClick={ () => this.deleteEntry(entry.id) } className="removeEntry">‚úñ</button>
                                <p> Date: {entry.date}</p>
                                <h3>{entry.title}</h3>
                                <p>{entry.comment}</p>
                                <p>{entry.signature} </p>
                            </div>
                        )
                    })
                }

            </div >
        );
    }
}
export default Entries;